/* Split the screen in half */
.split {
  height: 90%;
  position: fixed;
  z-index: 1;
  top: 0;
  overflow-x: hidden;
  padding-top: 20px;
  padding-bottom: 20px;
}

/* Control the left side */
.left {
  left: 0;
  width: 65%;
}

/* Control the right side */
.right {
  right: 0;
    width: 35%;
}
